<template>
    <div class="wrapper">

        <!--<text>{{msg}}</text>-->
        <!--<text>{{userId}}</text>-->

        <!--<WxcButton text="传参"-->
        <!--@wxcButtonClicked="wxcButtonClicked"></WxcButton>-->

        <wxc-tab-bar :tab-titles="tabTitles"
                     :title-styles="tabStyles"
                     title-type="icon"
                     @wxcTabBarCurrentTabSelected="wxcTabBarCurrentTabSelected">
            <div class="item-container" :style="contentStyle">
                <text>首页</text>
            </div>
            <div class="item-container" :style="contentStyle">
                <text>特别推荐</text>
            </div>
            <div class="item-container" :style="contentStyle">
                <text>消息中心</text>
            </div>
            <div class="item-container" :style="contentStyle">
                <text>我的主页</text>
            </div>

        </wxc-tab-bar>

    </div>

</template>

<script>
    import {WxcButton, WxcMinibar, WxcTabBar, Utils} from 'weex-ui';
    import tabBarConfig from '../views/tabbarConfig';

    import {getQueryVariable} from "../utils";

    const navigator = weex.requireModule('navigator');

    const storage = weex.requireModule('storage');

    export default {
        name: "index",
        // data() {
        //     return {
        //         msg: '111',
        //         userId: '',
        //         tabTitles: tabBarConfig.tabTitles,
        //         tabStyles: tabBarConfig.tabStyles
        //     }
        // },

        data: () => ({
            tabTitles: tabBarConfig.tabTitles,
            tabStyles: tabBarConfig.tabStyles
        }),
        components: {
            WxcButton,
            WxcMinibar,
            WxcTabBar
        },

        methods: {
            wxcButtonClicked() {
                const test = new BroadcastChannel('Hello');
                test.postMessage('1222222');
            },
            minibarLeftButtonClick() {

            },
            minibarRightButtonClick() {

            },
            wxcTabBarCurrentTabSelected() {

            }
        },
        created() {
            const tabPageHeight = Utils.env.getPageHeight();
            const { tabStyles } = this;
            this.contentStyle = { height: (tabPageHeight - tabStyles.height) + 'px' };

            storage.getItem('text', event => {
                this.msg = event.data;
            });

            this.userId = getQueryVariable('userId');
            navigator.setNavBarTitle({
                title: 'second weex'
            }, event => {
            });
        }
    }
</script>

<style scoped>
    .wrapper {
        /*position: absolute;*/
        /*top: 0;*/
        /*bottom: 0;*/
        /*left: 0;*/
        /*right: 0;*/
        /*background-color: #FFFFFF;*/
    }

    .item-container {
        width: 750px;
        /*background-color: #f2f3f4;*/
        background-color: red;
        align-items: center;
        justify-content: center;
        border-left-style: solid;
        border-bottom-style: solid;
        border-width: 3px;
    }

</style>


